<!-- 团队管理 -->
<template>
  <div class="list-common-box">
    <div class="title-box">
      <Breadcrumb :isLevel='2'></Breadcrumb>
      <div class="rightbox">
        <div class="item" @click="handleAdd()">
           <cc-svg-icon icon-class="add"></cc-svg-icon>
           <span>新增</span>
        </div>
      </div>
    </div>
    <!-- 主要数据 -->
    <div class="card-box">
      <!-- 搜索 -->
      <div class="search-bar">
        <!-- 左 -->
        <div class="search-left">
          <div class="search-item">
            <span class="search-label">团队名称</span>
            <el-input v-model.trim="searchKey.teamName" placeholder="请输入团队名称" clearable></el-input>
          </div>
          <div class="search-item">
            <span class="search-label">团队编码</span>
            <el-input v-model.trim="searchKey.teamId" placeholder="请输入团队编码" clearable></el-input>
          </div>
          <div class="search-item">
            <span class="search-label">所属机构</span>
            <treeselect :multiple="false" :options="OrganOptions"  placeholder="请选择机构" v-model="searchKey.registerOrganizationId" />
          </div>
          <div class="search-item">
            <span class="search-label">状态</span>
             <el-select placeholder="请选择状态" v-model="searchKey.teamStatus" clearable class="small-box">
              <el-option v-for="item in statusOption" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </div>
        </div>
        <div class="search-right">
          <el-button type="primary" plain icon="el-icon-search" @click="querylist()">查询</el-button>
          <el-button type="primary" plain icon="el-icon-delete" @click="Refreshlist()">清空</el-button>
        </div>
      </div>
      <!-- 表格 -->
      <div class="table-box">
        <el-table :data="dataList" tooltip-effect="dark" stripe height="100%"> 
          <el-table-column label="序号" align="center" width="80">
            <template slot-scope="scope">
              {{scope.$index+(pageNum - 1) * pageSize + 1}}
            </template>
          </el-table-column>
          <el-table-column prop="teamName" label="团队名称" align="center">
            <template slot-scope="scope">
              {{scope.row.teamName?scope.row.teamName:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="teamId" label="团队编码" align="center">
            <template slot-scope="scope">
              {{scope.row.teamId?scope.row.teamId:EMPTYTEXT}}
            </template>
          </el-table-column>
           <el-table-column prop="registerOrganization" label="所属机构"  align="center">
            <template slot-scope="scope">
              {{scope.row.registerOrganization?scope.row.registerOrganization:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="teamStatus" label="状态"  align="center" width="90">
            <template slot-scope="scope">
              {{scope.row.teamStatus===1?'启用':scope.row.teamStatus===2?'禁用':EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="createDateStr" label="创建时间"  align="center" width="170">
            <template slot-scope="scope">
              {{scope.row.createDateStr?scope.row.createDateStr:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="registerUser" label="创建人"  align="center">
            <template slot-scope="scope">
              {{scope.row.registerUser?scope.row.registerUser:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column label="操作"  align="center" width="120">
            <template slot-scope="scope">
              <cc-svg-icon icon-class="edit" @click="handleAdd(scope.row.teamId)"></cc-svg-icon>
              <cc-svg-icon icon-class="view" @click="handleDetail(scope.row.teamId)"></cc-svg-icon>
              <cc-svg-icon icon-class="maintain" @click="handleView(scope.row)"></cc-svg-icon>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <!-- 分页 -->
      <div class="page-box">
         <Paginaction class='pagination' :goPage="pageNum" :totalNum="totalNum" @updateList="changePage" ref="paginationEvent"/>
      </div>
    </div>
     <!-- 新增.编辑团队弹窗 -->
     <addTeam v-model="teamDialog" :id="teamId" @refresh="getDatalist"></addTeam>
     <!-- 团队详情 -->
     <teamDetail v-model="teamDetailDialog" :id="teamId"></teamDetail>
  </div>
</template>

<script>
  import addTeam from './addTeam' //新增、编辑团队
  import teamDetail from './detail' //团队详情
  import Treeselect from '@riophae/vue-treeselect' //树型select
  import '@riophae/vue-treeselect/dist/vue-treeselect.css'
  export default {
    name: 'organs-list',
    components: {
      Treeselect,
      addTeam,
      teamDetail
    },
    data() {
      return {
        statusOption:[
         {
           value:0,
           label:'全部'
         },
         {
           value:1,
           label:'已启用'
         },
         {
           value:2,
           label:'已禁用'
         }
       ], //状态下拉
        searchKey:{
          teamName:'',//团队名称
          teamId:'', //团队编码
          registerOrganizationId:null,//所属机构
          teamStatus:''//团队状态
        },
        pageNum:1,//当前页
        pageSize:10,//每页条数
        totalNum: 0, //总条数
        dataList: [], //团队列表
        teamDialog:false, //新增编辑团队弹窗
        teamId:'',//团队编码
        OrganOptions:[],//所属机构下拉
        teamDetailDialog:false, //团队详情弹窗
      }
    },
    created() {
       this.getOrganList();//获取所属机构列表
       this.getDatalist() //获取团队管理列表
    },
    methods: {
      //查看详情
      handleView(scope) {
        this.$router.push({
          path: '/basicData-manage/team-manage/teamMaintain?id=' + scope.teamId
        })
      },
      //新增编辑团队
      handleAdd(id){
        this.teamDialog = true
        this.teamId = id?id:''
      },
      //查看团队详情
      handleDetail(id,status){
        this.teamDetailDialog = true
        this.teamId = id
      },
      //查询
      querylist() {
        this.pageNum = 1
        this.getDatalist()
      },
      //重置
      Refreshlist() {
        this.pageNum = 1
        this.pageSize = 10
        Object.keys(this.searchKey).forEach(k => this.searchKey[k] = '')
        this.searchKey.registerOrganizationId = null
        this.getDatalist()
      },
      //分页改变
      changePage(query){
        this.pageNum = query.pageNum
        this.pageSize = query.pageSize
        this.getDatalist()
      },
      //获取所属机构列表
      getOrganList(){
        this.$http.post(process.env.VUE_APP_API+'/service/organization/getOrganization').then(res => {
          if(res.code===200){
          let initArrays = JSON.parse(JSON.stringify(res.data).replace(/organizationId/g, 'id').replace(/name/g, 'label').replace(/childrens/g, 'children'))
            this.OrganOptions = initArrays
          }else{
            this.OrganOptions = []
            this.$message.error(res.msg);
          }
        })
        .catch(error => {
          this.$message.error(error);
        });
      },
      //获取团队管理列表信息
      getDatalist() {
        this.$emit("viewLoad", true);
        let params = {}
        params.pageNum  = this.pageNum
        params.pageSize = this.pageSize
        params = Object.assign({},params,this.searchKey)
        this.$http.post(process.env.VUE_APP_API + '/family/team/getByPage', params).then(res => {
          if (res.code===200) {
            this.dataList = res.data.list || [] //团队列表
            this.totalNum = res.data.total //总条数
            this.pageNum = res.data.pageNum //当前页
            this.pageSize = res.data.pageSize //每页条数
          } else {
            this.$message.error(res.msg);
          }
          this.$emit("viewLoad", false);
        })
        .catch(error => {
          this.$emit("viewLoad", false);
          this.$message.error(error);
        });
      }
    }
  }
</script>
<style lang='less' scoped>
</style>