<template>
  <el-dialog title="团队详情" :visible.sync="show" center width="500px" @close="handleClose()" class="team-detail-box">
    <div v-if="userInfo" class="team-list-box">
       <ul class="team-list-box" v-if="userInfo">
          <li><span>团队名称：</span><label>{{userInfo.teamName||EMPTYTEXT}}</label></li>
          <li><span>团队编码：</span><label>{{userInfo.teamId ||EMPTYTEXT}}</label></li>
          <li><span>所属机构：</span><label>{{userInfo.registerOrganization||EMPTYTEXT}}</label></li>
          <li><span>状态：</span><label>{{userInfo.teamStatus?statusOption[userInfo.teamStatus]:EMPTYTEXT}}</label></li>
          <li>
            <span>团队成员：</span>
            <label class="table-box">
               <template v-if="userInfo.users.length>0">
                 <el-table :data="userInfo.users" border> 
                    <el-table-column  prop="userName" label="名字" align="center" width="120">
                      <template slot-scope="scope">
                        {{scope.row.userName?scope.row.userName:EMPTYTEXT}}
                      </template>
                    </el-table-column>
                    <el-table-column prop="userDuty" label="职位" align="center">
                      <template slot-scope="scope">
                        {{scope.row.userDuty?utils.formatSplitNumber(scope.row.userDuty,userDutyOption).getStr:EMPTYTEXT}}
                      </template>
                    </el-table-column>
                  </el-table>
              </template>
              <template v-else>暂无团队成员</template>
            </label>
          </li>
       </ul>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="handleClose()">关闭</el-button>
    </div>
  </el-dialog>
</template>

<script>
import utils from '../../../util/utils' //工具类
export default {
  name: 'team-detail',
  props: {
    value: {
      type: Boolean
    },
    id:{
      type:[String,Number],
      value:''
    }
  },
  model: {
    prop: 'value',
    event: 'change'
  },
  watch: {
    value (n) {
      this.show = this.value
      if(this.show === true){
        this.getTeamDetail();//获取团队详情
      }
    }
  },
  data () {
    return {
       utils:utils,//工具类
       show:this.value,
       userDutyOption:{
         1:"签约",
         2:"履约",
         4:"专项" 
       }, //职位集合
       statusOption:{
         1:"正常",
         2:"禁用"
       }, //状态下拉
       userInfo:null
    }
  },
  methods: {
    //获取团队详情
    getTeamDetail(){
      this.$http.get(process.env.VUE_APP_API+'/family/team/getDtoByTeamId?teamId='+this.id).then(res => {
        if(res.code===200){
          this.userInfo = res.data
        }else{
          this.$message.error(res.msg);
        }
      })
      .catch(error => {
        this.$message.error(error);
      });
    },
    //关闭弹窗
    handleClose(formName) {
      this.show = false
      this.$emit('change', false)
    }
  }
}
</script>

<style lang='less'>
.team-detail-box{
  .el-dialog--center .el-dialog__body{
    padding:30px 30px 30px 48px;
  }
  ul{
    display: flex;
    flex-wrap: wrap;
    flex-direction:column;
    >li{
      line-height:20px;
      list-style:none;
      margin-bottom:20px;
      font-size: 14px;
      color: rgba(0,0,0,0.85);
      display:flex;
      span{
       width:100px;
       text-align:right;
       padding-right:12px;
      }
      label{
        display:inline-block;
        flex:1;
        overflow: hidden; 
        text-overflow: ellipsis; 
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
      }
    }
  }
  .table-box{
    th{
      padding:0;
      line-height:34px;
    }
  }
}
</style>