<template>
  <div class="addTeam-box">
    <el-dialog :title="id?'修改团队':'新增团队'" :visible.sync="show" center width="550px" @close="resetForm('ruleForm')" class="teamForm-box" :close-on-click-modal="false">
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px">
        <el-form-item label="团队编码：" v-if="id">
          <p>{{ruleForm.teamId}}</p>
        </el-form-item>
        <el-form-item label="团队名称：" prop="teamName">
          <el-input v-model.trim="ruleForm.teamName" placeholder="请输入团队名称" class="input-box" clearable maxlength="20"></el-input>
        </el-form-item>
        <el-form-item label="所属机构：" prop="registerOrganizationId">
          <treeselect :multiple="false" :options="organOptions"  placeholder="请选择上级机构" v-model="ruleForm.registerOrganizationId" class="input-box" @select="selectOrgan" @input="clearOrgan"/>
        </el-form-item>
        <el-form-item label="状态：" prop="teamStatus">
          <el-select v-model="ruleForm.teamStatus" placeholder="请选择状态" class="input-box" clearable>
            <el-option v-for="(item,index) in statusOption" :key="index" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="团队成员：" prop="users" class="table-box">
          <template v-if="ruleForm.users.length>0">
            <el-table :data="ruleForm.users" border> 
              <el-table-column  prop="userName" label="名字" align="center" width="120">
                <template slot-scope="scope">
                    {{scope.row.userName?scope.row.userName:EMPTYTEXT}}
                </template>
              </el-table-column>
              <el-table-column prop="userDuty" label="职位" align="center">
                <template slot-scope="scope">
                  {{scope.row.userDuty?utils.formatSplitNumber(scope.row.userDuty,userDutyOption).getStr:EMPTYTEXT}}
                </template>
              </el-table-column>
              <el-table-column label="操作" align="center" width="80">
                <template  slot-scope="scope">
                  <cc-svg-icon icon-class="delete" @click="handleDelete(scope.$index)"></cc-svg-icon>
                </template>
              </el-table-column>
            </el-table>
          </template>
          <el-button type="primary" plain @click="userDialog=true" class="addUser">添加团队成员</el-button>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="resetForm('ruleForm')">取 消</el-button>
        <el-button type="primary" @click="submitForm('ruleForm')">确 认</el-button>
      </div>
    </el-dialog>
    <!-- 添加团队成员弹窗 -->
    <selectUser v-model="userDialog" :userArr="ruleForm.users" @update="updateForminfo"></selectUser>
  </div>
</template>

<script>
import selectUser from './selectUser' //选择用户
import utils from '../../../util/utils' //工具类
import Treeselect from '@riophae/vue-treeselect' //树型select
export default {
  name: 'add-team',
  components:{
    Treeselect,
    selectUser
  },
  props: {
    value: {
      type: Boolean
    },
    id:{
      type:[String,Number],
      value:''
    }
  },
  model: {
    prop: 'value',
    event: 'change'
  },
  watch: {
    value (n) {
      this.show = this.value
      if(this.show === true){
        this.getOrganList();//获取机构下拉
        if(this.id){
          this.getTeamDetail(); //获取团队详情
        }else{
          this.initForm();
        }
        this.$nextTick(()=>{
          this.$refs['ruleForm'].clearValidate();
        })
      }
    }
  },
  data () {
    //验证用户
    const validateUsers  = (rule, value, callback) => {
      if(this.ruleForm.users.length===0){
        callback(new Error('请选择用户'));
      }else{
        callback()
      }
    };
    return {
       utils:utils,//工具类
       show:this.value,
       userDialog:false,//选择用户弹窗
       userDutyOption:{
         1:"签约",
         2:"履约",
         4:"专项" 
       }, //职位集合
       statusOption:[
          {
            value:1,
            label:'正常'
          },
          {
            value:2,
            label:'禁用'
         }
       ], //状态下拉
       organOptions:[],//所属机构下拉
       ruleForm:{
         teamName:'',//团队名称
         teamId:'',//团队编码
         registerOrganizationId:null, //所属机构id
         registerOrganization:'',//所属机构名称
         users:[], //团队成员集合
         teamStatus:1,//团队状态
       },
       userArr:[],//选中的团队
       rules: {
        teamName: [
          { required: true, message: '团队名称不能为空', trigger: 'blur' }
        ],
        registerOrganizationId: [
          { required: true, message: '请选择所属机构', trigger: 'change' }
        ],
        teamStatus: [
          { required: true, message: '请选择状态', trigger: 'change' }
        ],
        users:[
          { required: true, validator: validateUsers, trigger: 'change' }
        ]
      }
    }
  },
  methods: {
    //获取团队详情
    getTeamDetail(){
      this.$http.get(process.env.VUE_APP_API+'/family/team/getDtoByTeamId?teamId='+this.id).then(res => {
        if(res.code===200){
          let result = res.data
          for(let key in this.ruleForm){
            if(key==='registerOrganizationId'){
              this.ruleForm[key] = result[key]!=='0'?result[key]:null
            }else{
              this.ruleForm[key] = result[key]
            }
          }
        }else{
          this.$message.error(res.msg);
        }
      })
      .catch(error => {
        this.$message.error(error);
      });
    },
    //获取机构列表
    getOrganList(){
      this.$http.post(process.env.VUE_APP_API+'/service/organization/getOrganization').then(res => {
        if(res.code===200){
        let initArrays = JSON.parse(JSON.stringify(res.data).replace(/organizationId/g, 'id').replace(/name/g, 'label').replace(/childrens/g, 'children'))
          this.organOptions = initArrays
        }else{
          this.organOptions = []
          this.$message.error(res.msg);
        }
      })
      .catch(error => {
        this.$message.error(error);
      });
    },
    //所属机构选择
    selectOrgan(result){
      this.ruleForm.registerOrganizationId = result.id
      this.ruleForm.registerOrganization = result.label
    },
    //触发所属机构验证
    clearOrgan(){
      this.$refs.ruleForm.validateField('registerOrganizationId')
    },
    //选择成员后数据回填
    updateForminfo(result){
      this.ruleForm.users = result
      this.$refs.ruleForm.validateField('users')
    },
    //删除团队成员
    handleDelete(index){
      this.ruleForm.users.splice(index,1)
    },
    //保存设置
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let originData = JSON.parse(JSON.stringify(this.ruleForm))
          delete originData.users
          originData.usersJson = JSON.stringify(this.ruleForm.users)
          let params = {}
          params = Object.assign({},originData)
          if(this.id){
            params.teamId = this.id
            this.editTeam(params)
          }else{
            delete params.teamId
            this.addTeam(params)
          }
        }
      });
    },
    //添加团队
    addTeam(params){
      this.$http.post(process.env.VUE_APP_API+'/family/team/insert',params).then(res => {
        if(res.code===200){
          this.$message.success('添加成功');
          this.show = false
          this.$emit('change', false)
          this.$emit('refresh') //刷新列表
        }else{
          this.$message.error(res.msg);
        }
      })
      .catch(error => {
        this.$message.error(error);
      });
    },
    //编辑用户
    editTeam(params){
      this.$http.post(process.env.VUE_APP_API+'/family/team/update',params).then(res => {
        if(res.code===200){
          this.$message.success('修改成功');
          this.show = false
          this.$emit('change', false)
          this.$emit('refresh') //刷新列表
        }else{
          this.$message.error(res.msg);
        }
      })
      .catch(error => {
        this.$message.error(error);
      });
    },
    //初始化表单
    initForm(){
      Object.keys(this.ruleForm).forEach(k => this.ruleForm[k] = '')
      this.ruleForm.registerOrganizationId = null
      this.ruleForm.teamStatus = 1
      this.ruleForm.users = []
    },
    //取消设置
    resetForm(formName) {
      this.show = false
      this.$emit('change', false)
    }
  }
}
</script>

<style lang='less'>
  /* 团队弹窗 */
  .teamForm-box{
    .el-dialog--center .el-dialog__body{
      padding:30px 30px 30px 48px;
    }
   .title{
     	font-size: 22px;
      color: #746651;
   }
   .input-box{
     width:290px;
   }
   .table-box{
     .el-table{
       margin-bottom:15px;
     }
     th{
       padding:0px;
     }
   }
  }
</style>