<template>
  <el-dialog title="选择团队成员" :visible.sync="show" center width="780" @close="handleCancel()" class="selectuser-box" :close-on-click-modal="false">
    <div class="flex-column">
      <!-- 表格 -->
      <div class="table-box flex-overflow">
        <el-table ref="multipleTable" :data="dataList" tooltip-effect="dark" stripe height="100%" @select="handleSelectionChange"> 
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column  prop="realName" label="姓名" align="center" width="120">
            <template slot-scope="scope">
                {{scope.row.realName?scope.row.realName:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="gender" label="性别" align="center" width="80">
            <template slot-scope="scope">
              {{scope.row.gender?genderOption[scope.row.gender]:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="idCard" label="身份证号" align="center">
            <template slot-scope="scope">
              {{scope.row.idCard?scope.row.idCard:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column label="操作" align="center" width="240">
            <template slot-scope="scope">
              <el-checkbox-group v-model="scope.row.userDuty" @change="changeUserDuty($event,scope.row)">
                <el-checkbox v-for="(item,key) in userDutyOption" :key="key" :label="+key" :disabled="!scope.row.isSelect">{{item}}</el-checkbox>
              </el-checkbox-group>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <!-- 分页 -->
      <div class="page-box">
        <Paginaction class='pagination' :goPage="pageNum" :totalNum="totalNum" @updateList="changePage" ref="paginationEvent"/>
      </div>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="handleCancel()">取 消</el-button>
      <el-button type="primary" @click="handleSave()">确 认</el-button>
    </div>
 </el-dialog>
</template>

<script>
import utils from '../../../util/utils'
export default {
  name: 'selectUser',
  props: {
    value: {
      type: Boolean
    },
    userArr:{
      type:Array,
      default: ()=>[]
    }
  },
  model: {
    prop: 'value',
    event: 'change'
  },
  watch: {
    value (n) {
      this.show = this.value
      if(this.show === true){
        this.getDatalist()
      }
    }
  },
  data () {
    return {
      utils:utils,//工具类
      show:this.value,
      userDutyOption:{
        1:"签约",
        2:"履约",
        4:"专项" 
      }, //职位集合
      genderOption:{
        0:"未说明",
        1:"男",
        2:"女",
        9:"未知"
      }, //机构类型
      pageNum:1,//当前页
      pageSize:10,//每页条数
      totalNum: 0, //总条数
      dataList:[],//用户列表
      selectUserList:[],//选中用户列表
    }
  },
  methods: {
    //分页改变
    changePage(query){
      this.pageNum = query.pageNum
      this.pageSize = query.pageSize
      this.getDatalist()
    },
    //获取人员信息列表
    getDatalist() {
      this.$emit("viewLoad", true);
      let params = {}
      params.pageNum  = this.pageNum
      params.pageSize = this.pageSize
      params.projectType = 2
      params = Object.assign({},params,this.searchKey)
      this.$http.post(process.env.VUE_APP_API + '/manager/user/getUserCollectionByPage', params).then(res => {
        if (res.code===200) {
          this.dataList = res.data.list || [] //人员信息列表
          this.totalNum = res.data.total //总条数
          this.pageNum = res.data.pageNum //当前页
          this.pageSize = res.data.pageSize //每页条数
          //数据回填
           this.$nextTick( ()=> {
            this.selectUserList = this.userArr.length>0?this.userArr:[]
            this.dataList.forEach(item=>{
              this.$set(item,'userDuty',[])
              this.$set(item,"isSelect",false)
              if(this.selectUserList.length>0){
                this.selectUserList.forEach(it=>{
                  if(item.accountNo===it.accountNo){
                    this.$refs.multipleTable.toggleRowSelection(item,true);
                    this.$set(item,'userDuty',this.utils.formatSplitNumber(it.userDuty,this.userDutyOption).getArr)
                    this.$set(item,"isSelect",true)
                  }
                })
              }
            })
          })
        } else {
          this.$message.error(res.msg);
        }
        this.$emit("viewLoad", false);
      })
      .catch(error => {
        this.$emit("viewLoad", false);
        this.$message.error(error);
      });
    },
    //表格某项选中
    handleSelectionChange(result) {
      this.selectUserList = []
      result.forEach(item=>{
        let obj = {}
        obj.accountNo = item.accountNo
        obj.userDuty = this.formatUserDuty(item.userDuty)
        obj.userName = item.realName
        this.selectUserList.push(obj)
      })
     //选中取消时职位的变化
     this.dataList.forEach(item=>{
        let select = this.selectUserList.find(it=>item.accountNo===it.accountNo)
        if(select){
          item.isSelect = true
        }else{
          item.isSelect = false
        }
        if(item.isSelect===false){
          item.userDuty = []
        }
      })
    },
    //当前列值选中
    changeUserDuty(val,row){
      this.selectUserList.forEach(item=>{
        if(item.accountNo === row.accountNo){
          item.userDuty = this.formatUserDuty(val)
        }
      })
    },
    //处理职位
    formatUserDuty(arr){
      let num = 0
      arr.forEach(item=> num = num+item)
      return num===0?null:num
    },
    //保存设置
    handleSave() {
      if(this.selectUserList.length>0){
        this.show = false
        this.$emit('change', false)
        this.$emit('update',this.selectUserList) //更新数据
      }else{
        this.$notify({
          title: '提示',
          message:'请选择用户',
          type: 'warning'
        });
      }
    },
    //取消设置
    handleCancel() {
      this.show = false
      this.$emit('change', false)
    }
  }
}
</script>

<style lang='less'>
.selectuser-box{
  .el-dialog__body{
    max-height:initial;
  }
  .flex-column{
    height:520px;
  }
  .table-box{
    height:auto;
  }
}
</style>